import React from "react";
import axios from "axios";
import { Button, Grid, Typography, TextField } from "@mui/material";
import Text from "../components/Text";

const validator = {
  name: /^[-]{2,4}$/,
  studentId: /20[012][0-9]{5}/,
  phoneNumber: /01[0-9]-?[0-9]{3,4}-?[0-9]{4}/,
};

const PaymentForm = (props) => {
  const [name, setName] = React.useState("");
  const [studentId, setStudentId] = React.useState("");
  const [phoneNumber, setPhoneNumber] = React.useState("");
  const [previousDevCareer, setPreviousDevCareer] = React.useState("");
  const [message, setMessage] = React.useState("");

  const sendMessage = () => {
    const endpoint = "https://application.gistory.me";
    const info = {
      project_id: "gsainfoteam_2022",
      name: name,
      student_id: studentId,
      phone_number: phoneNumber,
      misc: [
        {
          question: "    ",
          answer: previousDevCareer,
        },
        {
          question: "    ",
          answer: message,
        },
      ],
    };

    if (window.confirm("   ?"))
      axios({
        url: endpoint + "/api/application",
        method: "POST",
        data: info,
      })
        .then(function (response) {
          if (response.data.success) {
            window.alert(" ");
            props.handleNext();
          } else {
            window.alert(response.data.sms_error);
          }
        })
        .catch(function (error) {
          const errorInfo = error.response.data.error;
          switch (errorInfo) {
            case "application_not_opened":
              window.alert("   .");
              break;
            case "application_closed":
              window.alert(" ");
              break;
            case "sutdent_id_from_not_valid":
              window.alert(" .");
              break;
            case "phone_number_form_not_valid":
              window.alert("  .");
              break;
            case "duplicate_input_data":
              window.alert("   .");
              break;
            case "sms_error":
              window.alert("SMS   .");
              break;
            case "unknown_error":
              window.alert("    .");
              break;
            default:
              window.alert(" .");
              //        
              break;
          }
        });
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: "70%",
        maxWidth: "1000px",
      }}
    >
      <div
        style={{
          width: "100%",
          height: "1px",
          backgroundColor: "gray",
        }}
      />
      <Text size="45px" weight="900" marginTop="75px">
          
      </Text>
      <Typography variant="subtitle2" gutterBottom color="error" sx={{ mt: 2 }}>
        ,   
      </Typography>
      <Grid container spacing={3} sx={{ mt: 4 }}>
        <Grid item xs={12} md={5}>
          <TextField
            required
            id="name"
            label=""
            value={name}
            name="name"
            onChange={(e) => {
              setName(e.target.value);
            }}
            error={!validator["name"].test(name)}
            /*helperText="2 ~ 4 "*/
            fullWidth
          />
        </Grid>
        <Grid item xs={12} md={6}>
          <TextField
            required
            id="student_id"
            label=""
            value={studentId}
            name="studentId"
            onChange={(e) => {
              setStudentId(e.target.value);
            }}
            error={!validator["studentId"].test(studentId)}
            fullWidth
          />
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            id="phone_number"
            label=" "
            value={phoneNumber}
            name="phoneNumber"
            onChange={(e) => {
              setPhoneNumber(e.target.value);
            }}
            error={!validator["phoneNumber"].test(phoneNumber)}
            fullWidth
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            multiline
            id="previous_dev_career"
            label="    "
            value={previousDevCareer}
            name="previousDevCareer"
            onChange={(e) => {
              setPreviousDevCareer(e.target.value);
            }}
            fullWidth
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            multiline
            id="message"
            label="    "
            value={message}
            name="message"
            onChange={(e) => {
              setMessage(e.target.value);
            }}
            fullWidth
          />
        </Grid>
      </Grid>
      <div>
        {/*<Button onClick={props.handleBack}>
          <i class="material-icons">arrow_back</i>
        </Button>*/}
        {validator["phoneNumber"].test(phoneNumber) &&
        validator["studentId"].test(studentId) &&
        validator["name"].test(name) ? (
          <Button
            variant="contained"
            color="primary"
            onClick={sendMessage}
            sx={{ mt: 5, mb: 8, alignSelf: "flex-start" }}
          >
            
          </Button>
        ) : (
          <>
            <Button variant="contained" disabled sx={{ mt: 5, mb: 8 }}>
              
            </Button>
          </>
        )}
      </div>
    </div>
  );
};

export default PaymentForm;
